 .navbar {
     width: 98%;
     height: 40px;
     position: fixed;
     display: flex;
     justify-content: space-between;
    /* border: 1px solid blue; */
     padding: 20px;
     align-items: center;
    margin:auto;
    background-color:lightcoral;
    color: black;
    border-bottom: 5px solid white;
    
 }

 .logo {
     color: black;
     font-size: 20px;
     font-weight: 200;
  font-family: Georgia, 'Times New Roman', Times, serif;
     text-decoration: none;

 }

 .links {
     color: white;
     display: flex;
     justify-content: flex-end;
     cursor: pointer;
     list-style: none;


 }

 .about,.contact,.skill,.project {
     text-decoration: none;
     color: black;
     font-size: 20px;
     padding: 10px 20px;
     margin: 0 10px;
     cursor: pointer;
 }



 .mobile_icon {
 display: none;
 }


 @media only screen and (max-width:380px) {
    .link_mobile {
        position: absolute;
         top: 75px;
         left: 0;
         list-style: none;
         width: 100%;
         display:block;
         transition: all 0.5s ease-out;
         background-color:lightcoral;
       
        
     }

     .contact,.skill,.project {
         color: white;
         width: 100%;
         text-align:center;
         transition: all 0.5s ease-out;
     }

     .logo {
         display: flex;
         position: absolute;
         top: 15px;
         left: 35px;
       
         font-size: 20px;
     }

     .mobile_icon {

         display: block;
         position: absolute;
         font-size: 30px;
         color: white;
         background-color:rgb(233, 97, 97);
         border: none;
         outline: none;
         top: 28px;
         right: 50px;
     }

 .links {
         color: white;
         display: none;
         cursor: pointer;
         list-style: none;
     } 


 }

