.main{
    height: 550px;
    display: flex;
    margin-top:85px;
    /* border: 1px solid red; */
   
}


.right_box{
   
    width: 50%;
    height: 100%;
    text-align:end;
    /* background-color: lightcoral; */
    /* color: white; */
    /* background-color:rgb(63, 63, 199); */
    /* border: 1px solid red;
    */

}
.right_box h1{
    margin-top: 150px;
    margin-right: 40px;
    font-size: 40px;
    color: lightcoral;
}
.right_box p{
    margin-right: 40px;
    font-size: 30px;
    margin-top: -20px;
}

.left_box{
   
    width: 50%;
    height:100%;
    background-color:rgb(250, 243, 243);
    border-left: 3px solid white;
}

.left_box img{
    width: 60%;
    height: 350px;
    border-radius:10%;
    margin-left:40px;
    margin-top: 100px;
   
    box-shadow: rgba(206, 136, 136, 0.25) 0px 14px 28px, rgba(235, 163, 163, 0.22) 0px 10px 10px;
}
.btn1{
    padding: 12px;
    border-radius: 20px;
   margin-right: 10px;
    font-size: 15px;
    background-color: lightcoral;
    font-weight: bold;
}

.btn2{
    padding: 12px;
    border-radius: 20px;
    width: 17%;
   font-size: 15px;
    margin-right: 40px;
    font-weight: bold;
   
 
}
.about_me{
    height: 400px;
    display: flex;
    text-align: center;
}
.about_me h1{
    color: rgb(216, 110, 110);;
}
.right_about{
    width: 60%;
    height: 80%;
    padding: 40px 20px 20px 80px;
    
   text-align: left;
    /* border: 1px solid red; */
}
.left_about{
    width: 40%;
    height:80%;
    padding: 30px;
   
}

.left_about img{
    width: 100%;
    height:100%;
}
.right_about h1{
    font-size: 40px;
}
.right_about p{
    font-size: 20px;
    font-weight: 600;
    text-align: justify;
}
.count_project{
    display: flex;
    width: 90%;
    height:620px;
    margin: auto;

    /* border: 1px solid green; */
}

.count_project div{
    margin-top: 80px;
    width: 30%;
    margin-right: 50px;
    border: 1px solid green;
    text-align: center;
    border-radius: 20px;
    background-color: lightcoral;

}
.project_h1{
    margin: auto;
    width: 20%;
    color: rgb(216, 110, 110);
   
}
.project_one h4{
    font-size: 20px;
  
}
.project_one img {
width:100%;
height: 180px;
}
.project_one p{
    font-size: 15px;
    
}
.tech{
    margin-top: 30px;
}
.live{
    padding: 10px;
    width: 30%;
    margin-right: 20px;
    border-radius: 20px;
   
}
.code{
    padding: 10px;
    width: 30%;
    border-radius: 20px;
    background-color: lightcoral;
    font-weight: bold;
    
}



