
.slide1{
    border: 1px solid red;
    max-width: 70%;
    height: 100px;
    padding: 2rem 1rem;
    text-align: center;
    border-radius: 10px;
    perspective: 100px;
   
}
.slide1 img{
    max-width: 70%;
    height: 50px;
    margin-left: 50px;
    
}
.slide1:hover{
    transform: translateZ(20px);
}
