.slide{
    height: 200px;
    width: 250px;
    display:flex;
    place-items: center;
  padding: 10px;
  perspective: 100px;

}
img{
    height: 100%;
    width: 100%;
    transition: transform 1s;
   
}
.image-slider{
    display: grid;
    place-items: center;
   
}
.image-slider-track{
    display: flex;
    width: calc(200px*6);
    animation: play 40s linear infinite;
}
.image{
    animation-play-state: paused;
}
@keyframes play{
    0%{
        transform: translateX(0);
    }
    100%{
        transform: translateX(calc(-250px*6));
    }
}
img:hover{
    transform:transform 1s;
}